<template>
  <div class="detail">
    <div class="detailbox">
      <div class="detaildh">
        <van-nav-bar title="详情页" left-text="返回" left-arrow @click-left="onClickLeft" />
      </div>
      <div class="datailbox-top">
        <div class="topimgbox">
          <img :src="getData.large_img" alt />
        </div>
        <div class="topimgtextbox">
          <div class="topimgtext">
            <strong>{{getData.name}}</strong>
            <p>{{getData.enname}}</p>
          </div>
          <div class="sc" :class="isscActive ? 'active':''" @click="scClick">
            <van-icon name="like-o" />
          </div>
        </div>
      </div>
      <div class="detailbox-body">
        <div class="moldsbox clearfix">
          <div class="moldbox clearfix" v-for="(item,index) in product" :key="index">
            <div class="mold">{{item.title}}</div>
            <div
              class="options"
              :class="v.isActive ? 'active':''"
              v-for="(v,i) in item.rules"
              :key="i"
              @click="optionsactive(v,index)"
            >{{v.title}}</div>
          </div>
        </div>

        <div class="explainbox">
          <p>商品描述</p>
          <div class="explainText" v-for="(item,index) in exData" :key="index">{{index+1}}、{{item}}</div>
        </div>
      </div>
      <div class="detailbox-foot">
        <div class="price">￥{{getData.price}}</div>
        <div class="addBox">
          <van-stepper theme="round" v-model="count" button-size="24" disable-input />
        </div>
      </div>
      <div>
        <van-goods-action>
          <van-goods-action-icon
            icon="bag-o"
            text="购物袋"
            :badge="shopbagCount ? shopbagCount: '0'"
            @click="goshopbag"
          />
          <van-goods-action-button @click="addShopbag" color="#3a63f1" type="warning" text="加入购物袋" />
          <van-goods-action-button color="#0f0fce" @click="shopNew" type="danger" text="立即购买" />
        </van-goods-action>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/detail.less";
import { utils } from "../assets/js/utils";
export default {
  created() {
    this.pid = this.$route.query.pid;
    this.getbypidData();
    //查找是否被收藏
    this.findlike();
    //查询购物车数目
    this.queryshopbag();
  },

  data() {
    return {
      pid: "",
      product: "",
      exData: "",
      getData: "",
      isscActive: false,
      count: "",
      shopbagCount: "",
    };
  },
  methods: {
    // 根据pid获取数据并渲染
    getbypidData() {
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "GET",
        url: "/productDetail",
        params: {
          appkey: this.appkey,
          pid: this.pid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 600) {
            this.getData = result.data.result[0];
            // 处理数据
            let ruleItems = ["tem", "sugar", "cream", "milk"];
            let ruleData = [];
            ruleItems.map((v) => {
              if (result.data.result[0][v] == "") {
                return;
              }
              let rule = {
                title: result.data.result[0][v + "_desc"],
                rules: [],
              };
              let r = result.data.result[0][v].split("/");

              r.map((v, i) => {
                let o = {
                  title: v,
                  isActive: i == 0,
                };
                rule.rules.push(o);
              });
              ruleData.push(rule);
            });
            result.data.result[0].rules = ruleData;
            this.exData = result.data.result[0].desc.split(/\n/);
          }
          this.product = result.data.result[0].rules;
        })
        .catch((err) => {});
    },
    //点击切换样式
    optionsactive(item, index) {
      if (item.isActive) {
        return;
      }

      for (let i = 0; i < this.product[index].rules.length; i++) {
        if (this.product[index].rules[i].isActive == true) {
          this.product[index].rules[i].isActive = false;
          break;
        }
      }
      item.isActive = true;
    },
    //点击收藏样式，函数
    scClick() {
      let tokenString = localStorage.getItem("CSTK");

      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: this.pid,
      });
      let url = this.isscActive ? "/notlike" : "like";
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "post",
        url: url,
        data,
      })
        .then((result) => {
          this.$toast.clear();
          this.$toast(result.data.msg);

          if (result.data.code == 700) {
            //未登录
            return this.$router.push({ name: "Login" });
          } else if (result.data.code == 800) {
            //收藏
            this.isscActive = true;
          } else if (result.data.code == 900) {
            //取消收藏
            this.isscActive = false;
          }
        })
        .catch((err) => {});
    },
    //查找是否被收藏
    findlike() {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "get",
        url: "/findlike",
        params: {
          appkey: this.appkey,
          tokenString,
          pid: this.pid,
        },
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 1000) {
            if (result.data.result.length == 1) {
              this.isscActive = true;
            }
          }
        })
        .catch((err) => {});
    },
    // 返回上一个界面
    onClickLeft() {
      this.$router.go(-1);
    },
    //查询购物袋商品数量
    queryshopbag() {
      let tokenString = localStorage.getItem("CSTK");
      this.axios({
        method: "get",
        url: "/findAllShopcart",
        params: {
          appkey: this.appkey,
          tokenString: tokenString,
        },
      })
        .then((result) => {
          //
          if (result.data.code == 5000) {
            this.shopbagCount = result.data.result.length;
          }
        })
        .catch((err) => {});
    },

    // 加入购物袋
    addShopbag(isBuy) {
      let tokenString = localStorage.getItem("CSTK");
      if (!tokenString) {
        return this.$router.push({ name: "Login" });
      }

      let rulesdata = this.product;
      let newrules = [];
      rulesdata.map((v) => {
        for (let i = 0; i < v.rules.length; i++) {
          if (v.rules[i].isActive) {
            newrules.push(v.rules[i].title);
          }
        }
      });

      // 序列化参数
      let data = utils.queryString({
        appkey: this.appkey,
        tokenString,
        pid: this.pid,
        count: this.count,
        rule: newrules.join("/"),
      });
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
        loadingType: "spinner",
      });
      this.axios({
        method: "post",
        url: "/addShopcart",
        data,
      })
        .then((result) => {
          this.$toast.clear();
          if (result.data.code == 3000) {
            if (result.data.status == 1) {
              this.shopbagCount++;
            }
            //立即购买
            if (isBuy === true) {
              this.$router.push({
                name: "Pay",
                query: { sids: result.data.sid },
              });
            }
          }
        })
        .catch((err) => {});
    },

    //跳转到购物袋
    goshopbag() {
      this.$router.push({ name: "Shopbag" });
    },

    //立即购买
    shopNew() {
      this.addShopbag(true);
    },
  },
};
</script>

<style lang="less" scoped>
</style>